import React from 'react';
import "./styles/WelcomingMessage.css";
import Form from "./Form";
import guillemets from "../Assets/guillemets.png";

export default class WelcomingMessage extends React.Component{
    constructor(props){
        super(props);
        this.message = "Ralis par les tudiants de Centrale Marseille, ce site te permettra de dcouvrir la vie tudiante et associative de notre cole. En te souhaitant bon courage pour les concours et bonne dcouverte !";
    }


    render() {
        return(
            <div className="welcomingMessage">
                <div className="titleWMGuillemets">
                    <p className="titleWM"> Bienvenue sur le site<br/> alpha de Centrale<br/> Marseille</p>
                    <img id="guillemets" src={guillemets} alt={"guillemmets"}/>
                </div>
                <p id={"pBaliseWM"}> {this.message} </p>
                <a href={"https://youtu.be/vHx_c62ugsk"} target="_blank" id={"BTNTeaserAlpha"}> Voir le teaser alpha 2021 </a>
            </div>
        )
    }
}
